import React from 'react'

export const Validate = (WrappedComponent) => {
  class ValidateHOC extends React.Component {
    state = {
      valid: false,
      value: '',
    }

    _handleChange = (e) => {
      const target = e.target.value
      this.setState({
        value: target,
        valid: !!target,
      })
    }

    _updateState = (title, bool) => {
      this.setState({
        value: title,
        valid: bool,
      })
    }

    render () {
      const {valid, value} = this.state
      return <WrappedComponent {...this.props}
                               validate={valid}
                               validateValue={value}
                               updateState={this._updateState}
                               handleValid={this._handleChange}/>
    }
  }

  return ValidateHOC
}
