import React, { Component } from 'react'
import { withStyles } from '@material-ui/core/styles'
import { TodoItemEdit } from './TodoItemEdit'
import { TodoItemDefault } from './TodoItemDefault'

const style = theme => ({
  doneItem: {
    background: theme.palette.dark,
    textDecoration: 'line-through',
    textDecorationColor: theme.palette.common.black
  },
  item: {
    background: theme.palette.background.paper,
  },
})

class TodoItemComponent extends Component {
  state = {
    editTodo: false,
  }

  _handleChange = (e) => this.props.handleValid(e)

  _toggleDone = ({id}) => this.props.doneTodo(id)

  _openEdit = ({title}) => {
    this.props.updateState(title, false)
    this.setState({
      editTodo: true,
    })
  }

  _closeEdit = () => {
    this.props.updateState('', false)
    this.setState({editTodo: false})
  }

  _editDone = ({id}) => {
    const {changeTodo, validateValue} = this.props
    changeTodo(id, validateValue)
    this.setState({editTodo: false})
  }

  _delete = ({id}) => this.props.deleteTodo(id)

  clickHandlers = {
    toggleDone: 'toggleDone',
    openEdit: 'openEdit',
    closeEdit: 'closeEdit',
    editDone: 'editDone',
    delete: 'delete',
  }

  clickHandlerMap = {
    [this.clickHandlers.toggleDone]: this._toggleDone,
    [this.clickHandlers.openEdit]: this._openEdit,
    [this.clickHandlers.closeEdit]: this._closeEdit,
    [this.clickHandlers.editDone]: this._editDone,
    [this.clickHandlers.delete]: this._delete,
  }

  _handleClick = (type, item) => () => this.clickHandlerMap[type](item)

  render () {
    const {item, classes, validate} = this.props
    const {editTodo} = this.state
    const activeClass = item.completed ? classes.doneItem : classes.item
    return (
      <React.Fragment>
        {
          editTodo ?
            <TodoItemEdit handleChange={this._handleChange}
                          handleClick={this._handleClick}
                          clickHandlers={this.clickHandlers}
                          item={item}
                          validate={validate}
                          activeClass={activeClass}
            /> :
            <TodoItemDefault handleClick={this._handleClick}
                             clickHandlers={this.clickHandlers}
                             item={item}
                             activeClass={activeClass}
            />
        }
      </React.Fragment>
    )
  }

}

export const TodoItem = withStyles(style)(TodoItemComponent)
