import React from 'react'
import Grid from '@material-ui/core/Grid'
import { withStyles } from '@material-ui/core/styles'
import DialogContent from '@material-ui/core/DialogContent'
import TextField from '@material-ui/core/TextField'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import { Modal } from 'Components/Modal'
import { TodoList } from 'Components/TodoList'
import { Filter } from 'Components/Filter'

const styles = theme => ({
  inputStyles: {
    'width': theme.myVar.size['300'],
  },
})

const TodoGridComponent = (props) => {
  const {
    updateState,
    validateValue,
    handleValid,
    showModal,
    addTodo,
    validate,
    modalOpen,
    classes
  } = props

  const _closeModal = () => {
    updateState('', false)
    showModal(false)
  }

  const _addTodo = () => {
    addTodo(validateValue)
    _closeModal()
  }

  const _handleChangeText = (e) => handleValid(e)

  return (
    <div>
      <Grid container justify="center">
        <Grid item md={6}>
          <Filter/>
          <TodoList/>
        </Grid>
      </Grid>

      <Modal open={modalOpen}
             title={'Add new todo'}
             handleClose={_closeModal}>
        <DialogContent>
          <TextField className={classes.inputStyles}
                     autoFocus
                     margin="dense"
                     id="name"
                     label="Todo title"
                     type="text"
                     onChange={_handleChangeText}
                     fullWidth/>
        </DialogContent>
        <DialogActions>
          <Button onClick={_closeModal} color="primary">
            Cancel
          </Button>
          <Button onClick={_addTodo} disabled={!validate} color="primary">
            Add
          </Button>
        </DialogActions>
      </Modal>

    </div>
  )
}

export const TodoGrid = withStyles(styles)(TodoGridComponent)


